.card-eca {
  box-shadow: 0 20px 20px 0 rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  text-align: center;
  background-color: #f1f1f1;
  height: 550px;
  width: 250px;
  line-height: 250%;
  background: url(/business-portfolio/static/media/ECA.0998929f.png) no-repeat 0 1%;
