}

p {
  font-size: 14px;
}

.icon-img {
  width: 200px;
  opacity: 0.2;
}

.pw {
  margin-top: 40%;
}

.bw {
  margin-top: 20%;
}

.ecs {
  margin-top: 4%;
}

.eca {
  margin-top: 44%;
}

@media all and (max-width: 850px) {
  .services-contents {
    flex-direction: column;
  }

  .card-pw,
  .card-bw,
  .card-eca,
  .card-ecs {
    margin-bottom: 15px;
  }
}

.contact-contents {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  background: white;
}

.contact-title {
  color: rgb(0, 0, 0);
  font-size: 3em;
}

.contact-image {
  width: 700px;
}

form {
  width: 50%;
  margin: auto;
}

/* Style inputs */
input[type="text"],
input[type="email"],
select,
textarea {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

/* Style the submit button */
input[type="submit"] {
  width: 100%;
  background-color: #4eb5f1;
  box-shadow: 4px 5px 7px 0px rgb(0, 0, 0, 0.5);
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

/* Add a background color to the submit button on mouse-over */
input[type="submit"]:hover {
  background-color: #4095c6;
}

@media all and (max-width: 850px) {
  .contact-image {
    width: 150px;
  }

  .contact-title {
    color: rgb(0, 0, 0);
    font-size: 1.5em;
  }
  .contact-contents {
    flex-direction: column;
  }

  form {
    width: 90%;
    margin: auto;
    font-size: 14px;
  }

  input[type="text"],
  input[type="email"],
  select,
  textarea {
    padding: 6px 10px;
  }
}

.footer {
  width: 100%;
  height: 50vh;

  display: flex;
  justify-content: space-evenly;
  align-items: flex-start;
  flex-direction: column;

  background-color: black;
  color: white;
}

.footer-top {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
  height: 80%;
}

.footer-brand {
  width: 35%;
  height: 100px;
}

.footer-company {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  width: 30%;
  height: 100px;
}

.footer-company p {
  font-weight: bold;
  font-size: 18px;
  margin-bottom: 20px;
}

.footer-company ul {
  list-style-type: none;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.footer-company ul li {
  cursor: pointer;
}

.socials {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  width: 35%;
  height: 100px;
}

.socials-title {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 20px;
}

.socials-container {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 40%;
}

.socials-container div {
  padding: 5px;
}

.footer-bottom {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  height: 20%;
  width: 50%;
  background-color: black;
  margin: auto;
}

.footer-bottom span {
  font-size: 12px;
}

@media (max-width: 648px) {
  .footer-brand {
    font-size: 10px;
  }

  .footer-company p {
    font-size: 10px;
  }

  .footer-company li {
    font-size: 10px;
  }

  .footer-bottom {
    width: 100%;
  }

  .socials-title {
    font-size: 10px;
  }

  .footer-bottom span {
    font-size: 8px;
  }
}

